body{
  background-color: #24211e;
}
.App{
  background-color: #14110f;
  min-height:490px;
  width: 400px;
  margin: 2rem auto;
  /*margin: 40px auto;*/
  border-radius: 10px;
}

#to-do-form input{
  background-color: rgb(60, 71, 94);
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #eeea12; 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}

list[type=checkbox]:checked + label {
  text-decoration: line-through;
}

.title{
  text-align: center;
  color: #fff;
}